
import Vue from 'vue'
import App from './App.vue'
import VueRouter from "vue-router"

Vue.use(VueRouter)
const router = new VueRouter({
	mode : "hash",
	routes:[
    {
      path:'/',
      components: require ('./components/pages/home.vue'),
      name:"home",
      meta : {
          requiresAuth:true,
          reload: true
      },
  },
  {
    path:'/1',
    components: require ('./components/pages/project_1.vue'),
    name:"project_1",
    meta : {
        requiresAuth:true
    }
}
   ],

   scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



